.footer * {
    box-sizing: border-box;
}
.footer {
    margin-top: 15px;
    padding: 70px 0 0;
    background-color: rgb(183, 182, 181);
    box-sizing: border-box;
    width: 100%;
    font-size: 1em;
}
.footer .cont {
    max-width: 1170px;
    margin: auto;
}
.footer .row {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    color: rgb(255, 255, 255, 1);
}
.footer .row > div:nth-child(1) {
    width: 35%;
}
.footer .row > div {
    width: 20%;
    padding: 0 25px;
    display: flex;
    flex-direction: column;
    grid-gap: 15px;
    gap: 15px;
    flex-grow: 1;
}

.footer .row p {
    margin: 20px 0;
    color: rgba(39, 38, 38, 0.8);
    font-size: 1.2em;
    line-height: 1.8em;
}

.footer .row h4 {
    background-color: rgba(24, 21, 70, 0.2);
    color: #3a0ca3;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border-radius: 7px;
    padding: 5px 10px;
    font-weight: 600;
}

.footer .row ul,
.footer .row li {
    list-style-type: none;
    padding-left: 0;
    margin: 15px 0;
    transition: .3s;
    overflow: hidden;
}

.footer .row li {
    -webkit-transform: translateX(-15px);
            transform: translateX(-15px);
}
.footer .row li::before {
    content: '';
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
	width: 0;
	height: 0;
	border-top: 5px solid transparent;
	border-left: 8px solid rgb(118, 109, 244);
	border-bottom: 5px solid transparent;
}
.footer .row li:hover {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
.footer .row ul a {
    text-decoration: none;
    color: rgba(30, 29, 29, 0.8);
    margin-left: 15px;
}

.footer .row .socialButton {
    display: flex;
    align-items: center;
    grid-gap: 25px;
    gap: 25px;
    cursor: pointer;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
.footer .row .socialButton i {
    font-size: 28px;
    text-decoration: none;
    color: rgb(255, 255, 255);
    transition: .3s;
    opacity: .6;
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
.footer .row .socialButton i:hover {
    opacity: 1;
    -webkit-transform: translateY(-4px);
            transform: translateY(-4px);
}
